import { Component, OnInit, Output, Input } from '@angular/core';
import { MatChipInputEvent, MatTableDataSource } from '@angular/material';
import {COMMA, ENTER} from '@angular/cdk/keycodes';

@Component({
  selector: 'pxt-filter-mat-table',
  templateUrl: './pxt-filter-mat-table.component.html',
  styleUrls: ['./pxt-filter-mat-table.component.scss']
})
export class PxtFilterMatTableComponent implements OnInit {

  @Input() dataSource = new MatTableDataSource<any>();
  @Input() placeholder : String = "";
  constructor() { }

  ngOnInit() {
  }

  visible = true;
  selectable = true;
  removable = true;
  addOnBlur = true;
  readonly separatorKeysCodes: number[] = [ENTER, COMMA];
  filters: any[] = [];

  add(event: MatChipInputEvent): void {
    const input = event.input;
    const value = event.value;

    // Add our fruit
    if ((value || '').trim()) {
      this.filters = [];
      this.filters.push({name: value.trim()});
      this.applyFilter(value);
    }

    // Reset the input value
    if (input) {
      input.value = '';
    } 
  }

  applyFilter(filter: string) {
    filter = filter.trim();
    filter = filter.toLowerCase();
    this.dataSource.filter = filter;
  }

  remove(fruit: any): void {
    const index = this.filters.indexOf(fruit);
    if (index >= 0) {
      this.filters.splice(index, 1);
    }
    this.applyFilter('');
  }


  configureFilterPredicate() {
    this.dataSource.filterPredicate = (row, event) =>
      ( row.codigo.toString().indexOf(event) >= 0 ||
      row.nomeImagem.toString().indexOf(event) >= 0);

  }

}
